import React, { useEffect, useState, useRef } from "react";
import { Row, Col, Card, Button } from "reactstrap";
import { Camera } from "react-camera-pro";
import { CameraAlt } from "@mui/icons-material";
import "bootstrap/dist/css/bootstrap.min.css";
import "./Absen.css";
import useAxios from "../../hooks/useAxios";
import useAuth from '../../hooks/useAuth';


const Absen = () => {
    const api=useAxios();
    const camera = useRef(null);
    // const [image,setImage] = useState(null);
    const { auth } = useAuth();
    const [location, setLocation] = useState({ lat: null, lng: null });
    const [isCameraOpen, setIsCameraOpen] = useState(false);

    const getLocation = () => {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(
                (position) => {
                    setLocation({
                        lat: position.coords.latitude,
                        lng: position.coords.longitude,
                    });
                    console.log(position.coords.latitude, position.coords.longitude);
                },
                (err) => {
                    console.log(err);
                }
            );
        } else {
            console.log("Geolocation tidak didukung oleh browser ini.");
        }
    };

    useEffect(() => {
        console.log(location);
    }, []);

    const clockIn = () => {
        getLocation();
        setIsCameraOpen(true);
        console.log("Absen Masuk");
    };

    const takePhoto = async() => {
        const photo = camera.current.takePhoto();
        // setImage(photo);
        setIsCameraOpen(false);
        // console.log("Base64 Foto:", photo); // ✅ Langsung tampilkan di konsol

        await api.post(`dapi/hr/clock_in`, {poto:photo}).then((res)=>{
            console.log(res)
        })
    };

    return (
        <div className="absen-container">
            {/* Header */}
            <div className="head bg-primary text-white py-5 px-3 w-100">
                <Row className="align-items-center">
                    <Col>
                        <h3 className="fs-4">Halo, {auth?.user?.first_name}</h3>
                        <p>Selamat Pagi</p>
                    </Col>
                    <Col className="text-end">
                        <img
                            src="https://cdn.idntimes.com/content-images/post/20230515/gambar-profil-wa-keren-41d3732660868cf4a36d801d1301f672.jpg"
                            alt="Profile"
                            className="profile-img"
                        />
                    </Col>
                </Row>
            </div>

            {/* Menu */}
            <div className="bawah mt-n5">
                <Row className="text-center">
                    <Col>
                        <Card className="p-2">
                            <div className="menu-item">🕒</div>
                            <span className="text-muted small">Riwayat</span>
                        </Card>
                    </Col>
                    <Col>
                        <Card className="p-2">
                            <div className="menu-item">📄</div>
                            <span className="text-muted small">Pengajuan</span>
                        </Card>
                    </Col>
                    <Col>
                        <Card className="p-2">
                            <div className="menu-item">📆</div>
                            <span className="text-muted small">Cuti</span>
                        </Card>
                    </Col>
                </Row>

                {/* Absen */}
                <Card className="p-3 mt-3">
                    <span className="small mb-4">Selasa, 11 Maret 2025</span>

                    {/* Clock In */}
                    <Row className="d-flex align-items-center mb-3">
                        <Col>
                            <span className="small">
                                <strong>08:00 WIB</strong>
                                <br />
                                Actual Clock In
                            </span>
                        </Col>
                        <Col>
                            <Button color="warning" className="w-100" outline onClick={clockIn}>
                                Masuk ➜
                            </Button>
                        </Col>
                    </Row>

                    {/* Clock Out */}
                    <Row className="d-flex align-items-center mb-3">
                        <Col>
                            <span className="small">
                                <strong>17:00 WIB</strong>
                                <br />
                                Actual Clock Out
                            </span>
                        </Col>
                        <Col>
                            <Button color="info" className="w-100" outline>
                                Pulang ➜
                            </Button>
                        </Col>
                    </Row>
                </Card>
            </div>

            {/* Kamera Full Screen (Portrait) */}
            {isCameraOpen && (
                <div
                    style={{
                        position: "fixed",
                        top: 0,
                        left: 0,
                        width: "100vw",
                        height: "100vh",
                        backgroundColor: "#000",
                        zIndex: 9999,
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                    }}
                >
                    {/* Kamera */}
                    <div style={{ position: "relative", width: "100vw", height: "100vh" }}>
                        <Camera
                            ref={camera}
                            facingMode="user"
                            videoConstraints={{
                                width: 1080,
                                height: 1920,
                                facingMode: "user",
                            }}
                            style={{
                                width: "100%",
                                height: "100%",
                                objectFit: "cover",
                                zIndex: 1,
                            }}
                        />

                        {/* Tombol Ambil Foto */}
                        <button
                            type="button"
                            onClick={takePhoto}
                            style={{
                                position: "absolute",
                                bottom: "80px",
                                left: "50%",
                                transform: "translateX(-50%)",
                                width: "70px",
                                height: "70px",
                                borderRadius: "50%",
                                backgroundColor: "#fff",
                                display: "flex",
                                justifyContent: "center",
                                alignItems: "center",
                                boxShadow: "0 4px 8px rgba(0,0,0,0.3)",
                                border: "none",
                                zIndex: 2,
                                cursor: "pointer",
                            }}
                        >
                            <CameraAlt style={{ fontSize: 32, color: "#000" }} />
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
};

export default Absen;
